        expect(player.state.isMuted).toBe(false);
        expect(wrapper.find('div.msc-videoplayer__controls__volumeslider__container').hasClass('show')).toBe(true);

        wrapper
            .find('div.action-toggle')
            .at(1)
            .childAt(0)
            .simulate('click');
