        expect(player.state.isMuted).toBe(true);

        wrapper.find('div#VolumeSliderThumb').simulate('keyDown', { keyCode: KeyCodes.ArrowUp });
        // setting the state again to get complete code coverage
        wrapper.setState({isMuted: true});
        wrapper.find('div#VolumeSliderThumb').simulate('keyDown', { keyCode: KeyCodes.ArrowUp });
        expect(player.state.isMuted).toBe(false);

        wrapper.find('div#VolumeSliderThumb').simulate('keyDown', { keyCode: KeyCodes.ArrowDown });

        // Repeating this to fire the onVolumeChange Event with zero value
        wrapper.find('div#VolumeSliderThumb').simulate('keyDown', { keyCode: KeyCodes.ArrowDown });
        wrapper.find('div#VolumeSliderThumb').simulate('keyDown', { keyCode: KeyCodes.ArrowDown });
        expect(player.state.isMuted).toBe(true);

        // Media Events Test
